import React from "react";
import "../styles/GameTile.css";

function GameTile(props) {
  return (
    <div>
      <button
        className="GameTile"
        onClick={() => props.handleClick(props.name)}
      >
        <img src={props.src} alt={props.name} />
        <div>{props.name}</div>
      </button>
    </div>
  );
}

export default GameTile;
